@charset 'UTF-8';
.menus-list {
	padding: 0;
	list-style: none;
	margin: 40px -2px 0 0;
}

.menus-list .menu-actions {
	display: none;
}

.menus-list .menu-actions i {
	margin-right: 4px;
}

.menus-list li {
	padding: 4px;
	position: relative;
}

.menus-list li.current-menu .menu-actions {
	position: absolute;
	right: 10px;
	top: 2px;
	display: none;
}

.menus-list li.current-menu:hover .menu-actions {
	display: block;
}

.menus-list li a {
	height: 30px;
	position: relative;
}

.menus-list li a:hover {
	text-decoration: none;
}

.menus-list li form {
	display: inline-block;
}

.menus-list li form input {
	position: relative;
	width: 95%;
}

/*-----------------------------------*
  $TOAST-GRID

  An insane grid.
  You'd be mad to use it.

  Usage
  =====

  Assuming default values:

  <div class="grid">
    <div class="grid__col grid__col--1-of-2">
      A half-width column.
    </div>
    <div class="grid__col grid__col--1-of-4 grid__col--pull-1-of-4">
      A quarter, pulled left by its own width. You get this, right?
    </div>
  </div>


  Customisation
  =============

  $toast-grid-namespace and $toast-grid-column-namespace
  adjusts the class names for the grid. With
  default values, grid wrappers have a class
  of '.grid' and columns '.grid__col'.

  $toast-col-groups(n) adjusts column divisions.
  For example, $toast-col-groups(12) will produce
  a 12-column grid. $col-groups(3,6,8)
  will produce a 3-, 6-, and 8-column grid.

  $toast-gutter-width is—you guessed it—the gutter
  width. Accepts any unit.

  That's it. Have fun.

\*-----------------------------------*/
.grid {
	list-style: none;
	margin-left: -5px;
}

.grid__col--5-of-5 {
	width: 100%;
}

.grid__col--push-5-of-5 {
	margin-left: 100%;
}

.grid__col--pull-5-of-5 {
	margin-left: -100%;
}

.grid__col--1-of-5 {
	width: 20%;
}

.grid__col--push-1-of-5 {
	margin-left: 20%;
}

.grid__col--pull-1-of-5 {
	margin-left: -20%;
}

.grid__col--2-of-5 {
	width: 40%;
}

.grid__col--push-2-of-5 {
	margin-left: 40%;
}

.grid__col--pull-2-of-5 {
	margin-left: -40%;
}

.grid__col--3-of-5 {
	width: 60%;
}

.grid__col--push-3-of-5 {
	margin-left: 60%;
}

.grid__col--pull-3-of-5 {
	margin-left: -60%;
}

.grid__col--4-of-5 {
	width: 80%;
}

.grid__col--push-4-of-5 {
	margin-left: 80%;
}

.grid__col--pull-4-of-5 {
	margin-left: -80%;
}

.grid__col {
	box-sizing: border-box;
	display: inline-block;
	margin-right: -0.25em;
	min-height: 1px;
	padding-left: 5px;
	vertical-align: top;
}

@media (max-width: 700px) {
	.grid__col {
		display: block;
		margin-left: 0;
		margin-right: 0;
		width: auto;
	}
}

@media (max-width: 700px) and (min-width: 480px) {
	.grid__col[class*='grid__col--m-'] {
		display: inline-block;
		margin-right: -0.24em;
	}
	.grid__col.grid__col--m-1-of-2,
	.grid__col.grid__col--m-2-of-4 {
		width: 50%;
	}
	.grid__col.grid__col--m-1-of-3 {
		width: 33.33333%;
	}
	.grid__col.grid__col--m-2-of-3 {
		width: 66.66667%;
	}
	.grid__col.grid__col--m-1-of-4 {
		width: 25%;
	}
	.grid__col.grid__col--m-3-of-4 {
		width: 75%;
	}
}

@media (max-width: 480px) {
	.grid__col[class*='grid__col--s-'] {
		display: inline-block;
		margin-right: -0.24em;
	}
	.grid__col.grid__col--s-1-of-2,
	.grid__col.grid__col--s-2-of-4 {
		width: 50%;
	}
	.grid__col.grid__col--s-1-of-3 {
		width: 33.33333%;
	}
	.grid__col.grid__col--s-2-of-3 {
		width: 66.66667%;
	}
	.grid__col.grid__col--s-1-of-4 {
		width: 25%;
	}
	.grid__col.grid__col--s-3-of-4 {
		width: 75%;
	}
}

.grid__col--centered {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.grid__col--d-first {
	float: left;
}

.grid__col--d-last {
	float: right;
}

.grid--no-gutter {
	margin-left: 0;
	width: 100%;
}

.grid--no-gutter .grid__col {
	padding-left: 0;
}

.grid--no-gutter .grid__col--span-all {
	margin-left: 0;
	width: 100%;
}

.grid__col--ab {
	vertical-align: bottom;
}

.grid__col--am {
	vertical-align: middle;
}

.grid .grid__col {
	margin-right: -0.25rem;
}